const dataArrayFilter = (dataQuantity, indexQuantity) => {
    const result = data
        .map((e, i) => {
            let arr = [];
            if (i % dataQuantity === 0) {
                data.slice(i, i + dataQuantity).map(g => arr.push(g));
            }
            return arr;
        })
        .filter(val => val[0] !== undefined)
        .splice(0, indexQuantity);
    return result;
};

export const gameAdvert = data.filter((v, i) => i < 4);

export const gamePopular = dataArrayFilter(2, 10);

export const gameSuggest = dataArrayFilter(3, 10);
